
@media screen and (max-width: 480px) {
#main_contents{
    width: 375px;
    margin-left: auto;
    margin-right: auto;
}
}
@media screen and (min-width:480px) {
#main_contents{
    width: 100%;
    height: auto;
    min-height: 840px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: auto;
    margin-right: auto;
}
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
*{
  margin: 0px;
  padding: 0px;
}



